import {
  createUserWithEmailAndPassword,
  GoogleAuthProvider,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signInWithPopup,
  signOut,
  updateProfile,
} from "firebase/auth";
import React, { createContext, useEffect, useState } from "react";
import { auth } from "../auth/firebase";
import { toastError, toastSuccess } from "../helpers/ToastNotify";
import { useNavigate } from "react-router-dom";

export const AuthContextt = createContext();

const AuthContext = ({ children }) => {
  const navigate = useNavigate();
  const [currentUser, setCurrentUser] = useState();

  useEffect(() => {
    userTakip();
  }, []);

  const createUser = async (email, password, displayName) => {
    try {
      await createUserWithEmailAndPassword(auth, email, password);
      toastSuccess("Registered Successfully!");
      navigate("/");

      //? USERTAKPTEN SONRA -----kullanc profilini gncellemek iin kullanlan firebase metodu, login logout da userTakip sayesinde gncelleniyor ama register da isim gncellemesi yok, o da bu ekilde oluyor.alttakini yazmazsam (register ile girdiimde) navbarda displayName i gremem. alttakini yazmazsam sadece google ile girersem grrm
      await updateProfile(auth.currentUser, {
        displayName: displayName,
      });
    } catch (error) {
      toastError(error.message);
    }
  };

  const signInUser = async (email, password) => {
    try {
      await signInWithEmailAndPassword(auth, email, password);
      toastSuccess("Logined Successfully");
      navigate("/");
    } catch (error) {
      toastError(error.message)
    }

  };

  const signInGoogle = () => {
    const provider = new GoogleAuthProvider();

    signInWithPopup(auth, provider)
      .then((result) => {
        toastSuccess("Countunied with Google");
        navigate("/");
      })
      .catch((error) => {
        toastError("Can't Keep Google Account!");
      });
  };

  const cikis = () => {
    signOut(auth);
    toastSuccess("Log Outted!");
  };

  const userTakip = () => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        const { email, displayName, photoURL } = user;

        setCurrentUser({
          email: email,
          displayName: displayName,
          photoURL: photoURL,
        });
      } else {
        setCurrentUser(false);
      }
    });
  };

  return (
    <AuthContextt.Provider
      value={{ createUser, signInUser, signInGoogle, cikis, currentUser }}
    >
      {children}
    </AuthContextt.Provider>
  );
};

export default AuthContext;
